import React, { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import {
  getArticleDetail,
  getListArticleFilter,
  getListCategory,
  getNewest,
  getTopview,
} from "redux/slices/enhancementView";
import { checkTokenExpiration } from "contexts/TokenCheck";
import styled from "styled-components";
import TreeCategory from "./DetailView/TreeCategory";
import Details from "./DetailView/Details";
import TopView from "./HomeView/TopView";
import ResultFilter from "./SearchView/ResultFilter";
import axios from "axios";
import { BASE_URL_API } from "constants/constants";
import HeaderView from "./HeaderView";

const EnhancementDetail = () => {
  const { objectId, recordID, categoryId } = useParams();

  const dispatch = useDispatch();
  const {
    listCategory,
    listArticleTopview,
    listArticleNewest,
    articleDetail,
    objectFilter,
    isLoadMore,
  } = useSelector((state) => state.enhancementViewReducer);
  const { listKnowledgeSetting } = useSelector(
    (state) => state.knowledgeEnhancementReducer
  );
  const [config, setConfig] = useState({});

  const [list, setList] = useState([]);

  useEffect(() => {
    listKnowledgeSetting.forEach((setting) => {
      if (setting.article_setting.obj_article === objectId) {
        setConfig(setting);
      }
    });
  }, [listKnowledgeSetting, objectId]);

  useEffect(() => {
    dispatch(getListCategory({ object_id: objectId }));
    dispatch(getTopview({ object_id: objectId }));
    dispatch(getNewest({ object_id: objectId }));
  }, [objectId, dispatch]);

  useEffect(() => {
    if (recordID && objectId) {
      dispatch(
        getArticleDetail({
          object_id: objectId,
          record_id: recordID,
        })
      );
    }
  }, [recordID, objectId, dispatch]);
  useEffect(() => {
    if (categoryId && objectId) {
      dispatch(
        getListArticleFilter({
          object_id: objectId,
          data: {
            category: categoryId,
            page: 0,
            size: 10,
            sort: {
              field: "priority",
              order: "asc",
            },
          },
        })
      );
    }
  }, [dispatch, categoryId, objectId]);

  useEffect(() => {
    const checkToken = async () => {
      const isTokenValid = await checkTokenExpiration();
      if (Object.entries(config).length > 0) {
        axios
          .post(
            BASE_URL_API + "list-view/load-data",
            {
              object_id: config.category_setting.obj_category,
              current_page: 1,
              record_per_page: 10,
              search_with: {
                meta: [],
                data: "",
              },
            },
            {
              headers: {
                Authorization: isTokenValid,
              },
            }
          )
          .then((res) => {
            if (res.data.data) {
              let tem = [];
              res.data.data.forEach((category) => {
                tem.push({
                  label: category[config.category_setting.fld_category]?.value,
                  value: category._id,
                });
              });
              setList(tem);
            }
          })
          .catch((error) => Notification("error", error.response.data.error));
      }
    };
    checkToken();
  }, [config]);

  const findNodeParents = (tree, targetKey, parents = []) => {
    for (const node of tree) {
      const newParents = [...parents, node.title];
      if (node.key === targetKey) {
        return newParents;
      }
      if (node.children && node.children.length > 0) {
        const found = findNodeParents(node.children, targetKey, newParents);
        if (found) {
          return found;
        }
      }
    }
    return null;
  };
  const findCategoryKey = (detail, setting) => {
    let art = detail.article ? detail.article : {};
    const { article_setting } = setting;
    return art[article_setting?.fld_category]?.id_related_record
      ? art[article_setting?.fld_category]?.id_related_record
      : "";
  };

  return (
    <Wrap>
      <HeaderView
        objectId={objectId}
        listParent={findNodeParents(
          listCategory,
          findCategoryKey(articleDetail, config)
        )}
      />
      <Content>
        <TreeCategory
          listCategory={listCategory}
          list={list}
          setting={config.article_setting}
          config={config}
        />

        {!window.location.pathname.includes("/details/") ? (
          <ResultFilter
            objectFilter={objectFilter}
            isLoadMore={isLoadMore}
            list={list}
          />
        ) : (
          <Details
            article={articleDetail.article ? articleDetail.article : {}}
            stats={articleDetail.stats ? articleDetail.stats : {}}
            config={config}
            objectId={objectId}
            recordID={recordID}
          />
        )}
        <WrapView isDocument={config.is_document}>
          <TopView
            setting={config.article_setting}
            listArticleTopview={listArticleTopview}
            isTop={true}
            config={config}
          />
          <TopView
            setting={config.article_setting}
            listArticleTopview={listArticleNewest}
            isTop={false}
            config={config}
          />
        </WrapView>
      </Content>
    </Wrap>
  );
};

export default EnhancementDetail;

const Wrap = styled.div`
  padding: 24px 24px 0 24px;
  flex-wrap: wrap;
  display: flex;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
`;

// const RightWrap = styled.div`
//   max-height: calc(100vh - 186px);
//   overflow: auto;
//   margin-top: 24px;
// `;
const WrapView = styled.div`
  width: 400px;
  min-width: 400px;
  margin-left: 16px;
  display: ${(props) => (props.isDocument ? "none" : "block")};
  @media screen and (max-width: 1366px) {
    display: none;
  }
`;
