import React, { useState } from "react";
import styled from "styled-components";
import {
  CaretDownOutlined,
  PlusOutlined,
  MinusOutlined,
} from "@ant-design/icons";
import {
  Tooltip,
  Tree,
  Typography,
  Popover,
  Input,
  Dropdown,
  Checkbox,
  Collapse,
} from "antd";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import DeleteRoles from "assets/icons/roles/DeleteRoles.svg";
import EditRoles from "assets/icons/roles/EditRoles.svg";
import PlusRoles from "assets/icons/roles/PlusRoles.svg";
import FilterIcon from "assets/icons/knowledgeEnhancement/fillter.svg";
import { Radio } from "antd";
import { useNavigate, useParams } from "react-router";
import {
  getListCategorySuccess,
  updateDataSearch,
} from "redux/slices/enhancementView";
import ModalCategory from "../ModalCategory";
import { checkTokenExpiration } from "contexts/TokenCheck";
import axios from "axios";
import { BASE_URL_API } from "constants/constants";
import _ from "lodash";
import { Notification } from "components/Notification/Noti";
import { FileTextOutlined } from "@ant-design/icons";

const { Text } = Typography;
const { Search } = Input;
const { Panel } = Collapse;
const TreeCategory = ({ listCategory, list, setting, config }) => {
  const { t } = useTranslation();
  const { defaultBrandName } = useSelector((state) => state.brandNameReducer);
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const { objectId } = useParams();
  const [objectSearch, setObjectSearch] = useState({
    text_fields: ["title", "body", "tag", "article_id"],
    created_date: "all",
  });

  const [objectModal, setObjectModal] = useState({});

  const [openModal, setOpenmodal] = useState(false);
  const navigate = useNavigate();
  const [listExpandKey, setListExpandKey] = useState([]);
  const [listSelectedKey, setListSelectedKey] = useState([]);

  const onSelect = (selectedKeys, info) => {
    navigate(
      `/knowledge-base-enhancement/${objectId}/category/${info.node.key}`
    );
  };

  const hexToRGB = (color) => {
    if (color) {
      const r = parseInt(color.slice(1, 3), 16);
      const g = parseInt(color.slice(3, 5), 16);
      const b = parseInt(color.slice(5, 7), 16);

      return `rgb(${r}, ${g}, ${b},0.1)`;
    }
  };

  const action = (name, id) => (
    <WrapAction>
      <Tooltip title={t("common.add")}>
        <img
          onClick={(e) => {
            e.stopPropagation();
            e.preventDefault();
            setOpenmodal(true);

            let temp = { key: "add", name: name, id: id };
            setObjectModal(temp);
          }}
          src={PlusRoles}
          alt="add"
        />
      </Tooltip>

      <Tooltip title={t("common.edit")}>
        <img
          onClick={(e) => {
            e.stopPropagation();
            e.preventDefault();
            setOpenmodal(true);

            let temp = { key: "edit", name: name, id: id };
            setObjectModal(temp);
          }}
          src={EditRoles}
          alt="edit"
        />
      </Tooltip>

      <Tooltip title={t("common.delete")}>
        <img
          onClick={(e) => {
            e.stopPropagation();
            e.preventDefault();
            setOpenmodal(true);
            let temp = { key: "delete", name: name, id: id };
            setObjectModal(temp);
          }}
          src={DeleteRoles}
          alt="delete"
        />
      </Tooltip>
    </WrapAction>
  );

  const menu = (
    <PopupWrap>
      <Title>{t("knowledgeBase.sortBy")}</Title>
      <Checkbox.Group
        defaultValue={["title", "body", "tag", "article_id"]}
        value={objectSearch.text_fields}
        onChange={(value) => {
          setObjectSearch((prev) => ({
            ...prev,
            text_fields: value,
          }));
        }}
      >
        <Checkbox value="title">{t("knowledgeBase.title")}</Checkbox>
        <Checkbox value="body">{t("knowledgeBase.content")}</Checkbox>
        <Checkbox value="tag">{t("knowledgeBase.tag")}</Checkbox>
        <Checkbox value="article_id">ID</Checkbox>
      </Checkbox.Group>
      <Title>{t("knowledgeBase.time")}</Title>
      <CustomRadio
        defaultValue="all"
        value={objectSearch.created_date}
        onChange={(value) => {
          setObjectSearch((prev) => ({
            ...prev,
            created_date: value.target.value,
          }));
        }}
        color={hexToRGB(defaultBrandName.theme_color)}
      >
        <Radio.Button value="all">{t("knowledgeBase.all")}</Radio.Button>
        <Radio.Button value="day">{t("knowledgeBase.day")}</Radio.Button>
        <Radio.Button value="week">{t("knowledgeBase.week")}</Radio.Button>
        <Break />
        <Radio.Button value="month">{t("knowledgeBase.month")}</Radio.Button>
        <Radio.Button value="year">{t("knowledgeBase.year")}</Radio.Button>
      </CustomRadio>
      <ButtonWrap>
        <Button
          onClick={() => {
            setObjectSearch({
              text_fields: ["title", "body", "tag", "article_id"],
              created_date: "all",
            });
            setOpen(false);
          }}
        >
          {t("common.cancel")}
        </Button>
        <Button onClick={() => setOpen(false)}>
          {t("knowledgeBase.done")}
        </Button>
      </ButtonWrap>
    </PopupWrap>
  );

  const updateCategoryByArticle = (arr, key, listArticle) => {
    const updateCategory = arr.map((item) => {
      if (item.key === key) {
        return {
          ...item,
          children: [...item.children, ...listArticle],
        };
      } else if (item.children?.length > 0) {
        return {
          ...item,
          children: updateCategoryByArticle(item.children, key, listArticle),
        };
      }
      return item;
    });

    return updateCategory;
  };

  const deleteChildren = (arr, key) => {
    return arr
      .map((item) => {
        if (item.key === key) {
          return {};
        } else if (item.children && item.children.length > 0) {
          return {
            ...item,
            children: deleteChildren(item.children, key),
          };
        }
        return item;
      })
      .filter((item) => Object.keys(item).length !== 0);
  };
  const onDocumentSelect = async (selectedKeys, info) => {
    let temExpand = [];
    if (info.node.isArticle) {
      navigate(
        `/knowledge-base-enhancement/details/${objectId}/${info.node.key}`
      );
    } else {
      if (!listSelectedKey.includes(info.node.key)) {
        setListSelectedKey((prev) => [...prev, info.node.key]);
        const isTokenValid = await checkTokenExpiration();
        let temCategory = info.node.parentCategory
          ? info.node.parentCategory
          : info.node.key;
        let page = info.node.page ? info.node.page : 0;

        axios
          .post(
            BASE_URL_API +
              `knowledge-base-enhancement/${objectId}/article/list`,
            {
              category: temCategory,
              page: page,
              size: 10,
              category_search_type: "accuracy",
              sort: {
                field: "priority",
                order: "asc",
              },
            },
            {
              headers: {
                Authorization: isTokenValid,
              },
            }
          )
          .then((res) => {
            if (res.data.data) {
              let newArticles = [];
              res.data.data.records.forEach((article) => {
                if (
                  _.get(
                    article,
                    `${setting?.fld_category}.id_related_record`,
                    ""
                  ) === temCategory
                ) {
                  newArticles.push({
                    isArticle: true,
                    key: article._id,
                    children: [],
                    title: _.get(
                      article,
                      `${setting?.fld_title}.value`,
                      "No title"
                    ),
                  });
                }
              });

              if (
                page * 10 + res.data.data.records.length <
                res.data.data.total
              ) {
                page++;
                newArticles.push({
                  parentCategory: temCategory,
                  page: page,
                  key: Math.random().toString(),
                  children: [],
                  title: t("knowledgeBase.seeMore"),
                });
              }
              if (info.node.page) {
                let tem = deleteChildren(listCategory, info.node.key);
                dispatch(
                  getListCategorySuccess(
                    updateCategoryByArticle(tem, temCategory, newArticles)
                  )
                );
              } else {
                dispatch(
                  getListCategorySuccess(
                    updateCategoryByArticle(
                      listCategory,
                      temCategory,
                      newArticles
                    )
                  )
                );
              }
            }
            if (listExpandKey.includes(info.node.key)) {
              temExpand = listExpandKey.filter((key) => key !== info.node.key);
              setListExpandKey(temExpand);
            } else {
              temExpand = [...listExpandKey, info.node.key];
              setListExpandKey(temExpand);
            }
          })
          .catch((error) => Notification("error", error.response.data.error));
      } else {
        if (listExpandKey.includes(info.node.key)) {
          temExpand = listExpandKey.filter((key) => key !== info.node.key);
          setListExpandKey(temExpand);
        } else {
          temExpand = [...listExpandKey, info.node.key];
          setListExpandKey(temExpand);
        }
      }
    }
  };

  const renderListPanel = (list) =>
    list.map((parent) => (
      <Panel
        key={parent.key}
        header={
          <Popover
            content={action(parent.title, parent.key)}
            placement="right"
            trigger="hover"
            overlayClassName="popoverRoles"
          >
            <Text
              ellipsis={{ tooltip: parent.title }}
              onClick={(e) =>
                !config.is_document &&
                navigate(
                  `/knowledge-base-enhancement/${objectId}/category/${parent.key}`
                )
              }
            >
              {parent.title}
            </Text>
          </Popover>
        }
        style={{
          marginBottom: "24px",
          borderRadius: "10px",
          background: "#f9fafc",
          boxShadow: "0px 2px 8px 0px rgba(0, 0, 0, 0.15)",
        }}
      >
        <Tree
          expandedKeys={listExpandKey}
          showLine={{ showLeafIcon: false }}
          switcherIcon={<CaretDownOutlined />}
          onSelect={(selectedKeys, info) =>
            config.is_document
              ? onDocumentSelect(selectedKeys, info)
              : onSelect(selectedKeys, info)
          }
          onExpand={(selectedKeys, info) => {
            if (config.is_document) {
              onDocumentSelect(selectedKeys, info);
            } else {
              let temExpand = [];
              if (listExpandKey.includes(info.node.key)) {
                temExpand = listExpandKey.filter(
                  (key) => key !== info.node.key
                );
                setListExpandKey(temExpand);
              } else {
                temExpand = [...listExpandKey, info.node.key];
                setListExpandKey(temExpand);
              }
            }
          }}
          treeData={parent.children}
          titleRender={(nodeData) =>
            nodeData.page ? (
              <span>{nodeData.title}</span>
            ) : !nodeData.isArticle ? (
              <Popover
                content={action(nodeData.title, nodeData.key)}
                placement="right"
                trigger="hover"
                overlayClassName="popoverRoles"
              >
                <Text ellipsis={{ tooltip: nodeData.title }}>
                  {nodeData.title}
                </Text>
              </Popover>
            ) : (
              <Text ellipsis={{ tooltip: nodeData.title }}>
                <FileTextOutlined /> {nodeData.title}
              </Text>
            )
          }
        />
      </Panel>
    ));

  return (
    <Wrap color={hexToRGB(defaultBrandName.theme_color)}>
      <Header>{t("knowledgeBase.category")}</Header>
      <FilterWrap>
        <CustomInputSearch
          allowClear
          placeholder={`${t("knowledgeBase.search")}...`}
          onSearch={(e) => {
            dispatch(
              updateDataSearch({ ...objectSearch, text_search: e, page: 0 })
            );
            navigate(`/knowledge-base-enhancement/${objectId}/search`);
          }}
        />
        <CustomDropdown
          visible={open}
          overlay={menu}
          placement="bottomCenter"
          trigger="click"
          overlayClassName="object-view"
          onVisibleChange={() => setOpen(!open)}
        >
          <ButtonFilter>
            <img src={FilterIcon} alt="filter" />
          </ButtonFilter>
        </CustomDropdown>
      </FilterWrap>
      {/* <Tree
        showLine={{ showLeafIcon: false }}
        switcherIcon={<CaretDownOutlined />}
        onSelect={onSelect}
        treeData={listCategory}
        titleRender={(nodeData) => (
          <Popover
            content={action(nodeData.title, nodeData.key)}
            placement="right"
            trigger="hover"
            overlayClassName="popoverRoles"
          >
            <Text ellipsis={{ tooltip: nodeData.title }}>{nodeData.title}</Text>
          </Popover>
        )}
      /> */}
      <CustomCollapse
        onChange={(activeKey) => {
          if (activeKey.length > 0 && config.is_document) {
            let info = {
              node: {
                key: activeKey[activeKey.length - 1],
              },
            };
            onDocumentSelect(false, info);
          }
        }}
        expandIconPosition={"end"}
        bordered={false}
        expandIcon={({ isActive }) =>
          isActive ? <MinusOutlined /> : <PlusOutlined />
        }
      >
        {renderListPanel(listCategory)}
      </CustomCollapse>

      <ModalCategory
        setListSelectedKey={setListSelectedKey}
        openModal={openModal}
        setOpenmodal={setOpenmodal}
        list={list}
        objectModal={objectModal}
      />
    </Wrap>
  );
};

export default TreeCategory;

